.login  {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
}
.login > img {
    width: 100%;
}
.login > a {
    padding: 20px;
    background-color: rgb(30, 215, 96);
    border-radius: 99px;
    font-weight: 700;
    color: white;
    text-decoration: none;
}